import React from "react";
import { CustomButton } from "./customElems";
import { XlsPublish, PdfPublish } from "./LoanPublish";

export function LoanActionTable( {onScheduleAction}) {
    return (
        <div>
            <CustomButton id="generateTableBtn" onClick={onScheduleAction}><font color="black" ><b>Generate schedule!</b></font></CustomButton>
            <CustomButton id="publishTableXlsBtn" onClick={XlsPublish}><font color="black" ><b>save as xls</b></font></CustomButton>
            <CustomButton id="publishTablePdfBtn" onClick={PdfPublish}><font color="black" ><b>save as pdf</b></font></CustomButton>
        </div>
    );
}
