import * as XLSX from 'xlsx';
import jsPDF from 'jspdf'; 
import htmlToPdfmake from 'html-to-pdfmake'; 
import { useRef } from 'react'

export function XlsPublish(){
    console.log("exportTabletoexcel...")
    const table = document.getElementById('table_loandata');
    const workbook = XLSX.utils.table_to_book(table);
    console.log("TTESSTTTT...")
    XLSX.writeFile(workbook, 'table.xlsx');
}

export function PdfPublish(){
    const mysection = document.getElementById("loanHeaderSection");
    if (!mysection) { console.error('header section not found!'); return; }
    const doc = new jsPDF(); 
    //const pdfmakeHtml = htmlToPdfmake(mysection.innerHTML); 
    doc.html( 
        mysection, 
        { 
            html2canvas: { scale: 0.3 }, // Optional: Adjust the scale if needed 
            callback: function (doc) { 
                doc.save('element.pdf'); 
            } 
        }
    );
    alert("nothing2...")
    // doc.table(10, 10, pdfmakeHtml); 
    // doc.save('component.pdf'); 
}