import React from 'react';
import { CustomNumberInput, CustomDateInput, CustomCurrencyInput, CustomButton } from './customElems';
import { GetAmortizedCashList, CashFlow } from './cashflow';
import { LoanActionTable } from './LoanAction';

const roundCurrency = (number) => { return number.toFixed(2); };

export function LoanTable() {
  const [rows, setRows] = React.useState([]);

    const getSchedule = function (ev) {
        const table = document.getElementById('table_loandata'); 
        while (table.rows.length > 3) { 
          table.deleteRow(3); // Keep deleting from the fourth row 
        }
        //getElementById is a quick workaround. TODO update through the React App framework
        let principal = document.getElementById("loanAmount_input").value;
        console.log("PP", principal)
        let interest_per_year = document.getElementById("loanAmount_rate_annual").value;
        let num_years = document.getElementById("loanAmount_Num_Years").value;
        let amortized = CashFlow.Amortize( principal, interest_per_year, num_years);
        document.getElementById("inputPMTannualText").innerHTML=amortized["pmt"];
        let amortized_list=GetAmortizedCashList( principal, num_years, interest_per_year, "01/01/01", amortized["pmt"])
        addRows(amortized_list, principal)
      };
   
  const addRows = (amortized_list, principal) => { 
        let newRows = []
        newRows.push(
          <tr className="row-ro1">
          <td style={{textAlign: 'left', width: '0.4953in'}} className="cell-ce27">&nbsp;
          </td><td style={{textAlign: 'left', width: '0.8256in'}} className="cell-ce40">&nbsp;
          </td><td style={{textAlign: 'right', width: '0.8256in'}} className="cell-ce27">&nbsp;
          </td><td style={{textAlign: 'left', width: '1.1339in'}} className="cell-ce27">&nbsp;
          </td><td style={{textAlign: 'left', width: '0.2638in'}} className="cell-ce27">&nbsp;
          </td><td style={{textAlign: 'left', width: '0.8256in'}} className="cell-ce74">&nbsp;
          </td><td style={{textAlign: 'left', width: '0.9138in'}} className="cell-ce74"></td>
          <td id="tableData_initBalance" style={{textAlign: 'right', width: '1.211in'}} className="cell-ce27">{principal}</td>
        </tr>
        )
        for (let i = 0; i < amortized_list.length; i++) {
        newRows.push( 
            <tr className="row-ro1" key={rows.length}>
                <td style={{textAlign: 'left', width: '0.2638in'}} className="cell-ce27">{i}</td>
                <td style={{textAlign: 'left', width: '0.8256in'}} className="cell-ce40">{amortized_list[i].loan_due_date}</td>
                <td style={{textAlign: 'right', width: '0.8256in'}} className="cell-ce27">{roundCurrency(amortized_list[i].period_pmt)}</td>
                <td style={{textAlign: 'left', width: '1.1339in'}} className="cell-ce27">&nbsp;</td>
                <td style={{textAlign: 'left', width: '0.2638in'}} className="cell-ce74">&nbsp;</td>
                <td style={{textAlign: 'right', width: '0.8256in'}} className="cell-ce27">{roundCurrency(amortized_list[i].interest_fraction)}</td>
                <td style={{textAlign: 'right', width: '0.9138in'}} className="cell-ce27">{roundCurrency(amortized_list[i].principal_fraction)}</td>
                <td style={{textAlign: 'right', width: '1.211in'}} className="cell-ce27">{roundCurrency(amortized_list[i].balance_remaining_after)}</td>
          </tr>        
        ); // Add the new row to the existing list of rows 
      }
        setRows([...rows, newRows]);

  };

  return (
    <div>
        <LoanActionTable onScheduleAction={getSchedule}/>
        <h2><p>Amortization Schedule</p></h2>
        <table id="table_loandata" border="{0}" cellSpacing="{0}" cellPadding="{0}" className="table-ta2">
          <colgroup>
              <col width={55} />
              <col width={92} />
              <col width={92} />
              <col width={126} />
              <col width={29} />
              <col width={92} />
              <col width={101} />
              <col width={134} />
          </colgroup>
          <tbody>
            <tr className="row-ro3">
                <td style={{textAlign: 'left', width: '0.2638in'}} className="cell-ce26">
                  <p>No.</p>
                </td><td style={{textAlign: 'left', width: '0.8256in'}} className="cell-ce39">
                  <p>Due Date</p>
                </td><td style={{textAlign: 'right', width: '0.8256in'}} className="cell-ce39">
                  <p>Payment</p>
                  <p>Due</p>
                </td><td style={{textAlign: 'left', width: '1.1339in'}} className="cell-ce39">
                  <p>Additional Payment</p>
                </td><td style={{textAlign: 'left', width: '0.2638in'}} className="cell-ce69">&nbsp;</td><td style={{textAlign: 'left', width: '0.8256in'}} className="cell-ce73">
                  <p>Interest</p>
                </td><td style={{textAlign: 'left', width: '0.9138in'}} className="cell-ce73">
                  <p>Principal</p>
                </td><td style={{textAlign: 'left', width: '1.211in'}} className="cell-ce73">
                  <p>Balance</p>
                </td>
              </tr>
              <tr className="row-ro1">
                <td style={{textAlign: 'left', width: '0.4953in'}} className="cell-ce27">&nbsp;
                </td><td style={{textAlign: 'left', width: '0.8256in'}} className="cell-ce40">&nbsp;
                </td><td style={{textAlign: 'right', width: '0.8256in'}} className="cell-ce27">&nbsp;
                </td><td style={{textAlign: 'left', width: '1.1339in'}} className="cell-ce27">&nbsp;
                </td><td style={{textAlign: 'left', width: '0.2638in'}} className="cell-ce27">&nbsp;
                </td><td style={{textAlign: 'left', width: '0.8256in'}} className="cell-ce74">&nbsp;
                </td><td style={{textAlign: 'left', width: '0.9138in'}} className="cell-ce74"></td>
                <td style={{textAlign: 'right', width: '1.211in'}} className="cell-ce27"></td>
              </tr>
            { rows }
          </tbody>
        </table>
    </div>
    )
};
