
import Rate from './rate_processor';

class Cash 
{
  constructor(amount, nominal_discount_rate, at_period){
    this._amount=amount
    this._nominal_discount_rate = nominal_discount_rate
    this._at_period = at_period
  }
}

export class CashList 
{
  constructor()
  {
    this._cash_flow_list=[]
  }

  appendCash(amount, period, inflation_rate, real_discount_rate)
  {
    amount=parseFloat(amount)
    period=parseInt(period) //parseInt?
    inflation_rate=parseFloat(inflation_rate)
    real_discount_rate=parseFloat(real_discount_rate)
    var r;
    Rate.getNominalDiscountRate(real_discount_rate, inflation_rate, (x)=>{r=x} )
    this._cash_flow_list.push( new Cash(amount, r, period));
    this._cash_flow_list.sort((a,b) =>  a._at_period-b._at_period  );
  }

  get()
  {
    return this._cash_flow_list;
  }
};

export const CashFlow = {

  GetNPV: function ( cash_list, current_period=0)
  {
    //console.log("TTT ",this._cash_flow_list)
    let npv, current_cumulative_discount_rate=0.0
    for(const cash of cash_list){
      console.log("CCC ", cash)
      if(cash._at_period<current_period) { continue }
      if(cash._at_period===current_period) { npv=cash._amount; continue; }
      current_cumulative_discount_rate=(((1+current_cumulative_discount_rate)*(1+cash._nominal_discount_rate/100)-1)) //=(1+B34)*(1+C27)-1
      npv+=(cash._amount/(1+current_cumulative_discount_rate))//=C33/(1+C34)
    }
    console.debug(npv)
    return npv
  },

Amortize: function( principal, rate_per_period, num_of_periods) {
    principal=parseFloat(principal);
    rate_per_period=parseFloat(rate_per_period)/100;
    num_of_periods=parseInt(num_of_periods);
    console.log("AAA", principal, rate_per_period, num_of_periods )
    var pmt = (principal * rate_per_period) / (1 - ((1 + rate_per_period)**(-num_of_periods)) );
    var totalPayment = pmt * num_of_periods; 
    var totalInterest = totalPayment - principal;
    return {
      "pmt": pmt,
      "totalPayment" : totalPayment,
      "totalInterest" : totalInterest
    }
  },

  handleGetPVFromFV : function (fv, discount_rate_per_period, period, setResult)  {
    console.debug("Test")
    fv=parseFloat(fv)
    discount_rate_per_period=parseFloat(discount_rate_per_period)
    period=parseFloat(period)
    setResult( (fv/((1 + discount_rate_per_period/100)**period)) )
  },

  handleGetFVFromPV : function (pv, discount_rate_per_period, period, setResult)  {
    pv=parseFloat(pv)
    discount_rate_per_period=parseFloat(discount_rate_per_period)
    period=parseFloat(period)
    setResult( (pv*((1 + discount_rate_per_period/100)**period)) )
  },

  handleGetPVFromAnnuity : function (annuity, discount_rate_per_period, period, setResult) {
    annuity=parseFloat(annuity)
    discount_rate_per_period=parseFloat(discount_rate_per_period)
    period=parseFloat(period)
    setResult( (annuity*(1 - ((1+discount_rate_per_period/100)**(-period) ))/(discount_rate_per_period/100)) )
  }
};


export const Bond = {

  getBondPrice : function(facevalue, coupon_payment, discount_rate_per_period, num_of_periods_to_maturity){
    let par = parseFloat(facevalue)
    let pmt = parseFloat(coupon_payment)
    let r=parseFloat(discount_rate_per_period)/100
    let T=parseInt(num_of_periods_to_maturity)
    var bond = (pmt * (1-((1+r)**(-T)) )/r) 
    bond+= par/((1+r)**(T))
    return bond
  }

};


export class CashAt
{
  constructor ( 
        loan_num_of_years,
        loan_apr,
        period_pmt,
        loan_due_date,
        additional_payment,
        balance_remaining_before
      ) 
    {
    this.loan_years = loan_num_of_years;
    this.loan_apr = loan_apr;
    this.period_pmt =  period_pmt;
    this.due_date = loan_due_date;
    this.additional_payment = additional_payment;
    this.balance_remaining_before = balance_remaining_before
    const interest_rate_per_period=this.loan_apr/12;
    this.interest_fraction = this.balance_remaining_before * (interest_rate_per_period/100)
    this.principal_fraction = this.period_pmt - this.interest_fraction;
    this.balance_remaining_after = balance_remaining_before - this.principal_fraction
  }

  getNextCashAt ( )
  {
    //this.balance_remaining_after = this.principal_remaining_before - this.principal_fraction
    let next_cashat = new CashAt(
                          this.loan_years,
                          this.loan_apr,
                          this.period_pmt,
                          this.due_date+1,
                          this.additional_payment,
                          this.balance_remaining_after
                        );
    return next_cashat;
  }
};

export function GetAmortizedCashList( principal, num_of_years, apr, first_payment_date, pmt)
{
  let cash_list = []
  for(let i=0; i< num_of_years*12; i++){
    if(i===0){
      cash_list.push(new CashAt(
                          num_of_years,
                          apr,
                          pmt/12, //convert to per month
                          first_payment_date,
                          0,//additional_payment,
                          principal
                      ));     
      }else{
        cash_list.push( cash_list[i-1].getNextCashAt())
      }
  }
  return cash_list;
}