import React from 'react';


export const CustomNumberInput = ({ id, placeholder, ...props }) => {
    return (
      <input
        type="number"
        min="0.01"
        id={id}
        placeholder={placeholder}
        style={{ textAlign: 'right' }}
        {...props}
      />
    );
  };

export const CustomCurrencyInput = ({ id, placeholder, ...props }) => {
  return (
    <input
      type="number"
      min="0.01"
      id={id}
      placeholder={placeholder}
      style={{ textAlign: 'right' }}
      {...props}
    />
  );
};

export const CustomDateInput = ({ id, placeholder, ...props }) => { 
    return ( 
        <input 
            type="date" 
            id={id} 
            placeholder={placeholder} 
            style={{ textAlign: 'center' }} 
            {...props} 
        /> 
    ); 
};

export const CustomButton = ({ id, onClick, children }) => {
  const buttonStyle = {
    padding: '8px 16px',
    backgroundColor: '#a7d4c6',
    border: '1px solid #ccc',
    borderRadius: '3px',
    boxShadow: 'inset 1px 1px #fff, inset -1px -1px #888',
    fontSize: '14px',
    cursor: 'pointer'
  };

  const buttonHoverStyle = {
    ...buttonStyle,
    backgroundColor: '#e0e0e0',
  };

  const [style, setStyle] = React.useState(buttonStyle);

  return (
    <button
      id={id}
      onClick={onClick}
      style={style}
      onMouseOver={() => setStyle(buttonHoverStyle)}
      onMouseOut={() => setStyle(buttonStyle)}
    >
      {children}
    </button>
  );
};

