const DynamicTable = () => {
  // Initialize state for rows
  const [rows, setRows] = React.useState([]);

  // Function to add a new row
  const addRow = () => {
    const newRow = { id: rows.length + 1, value: '' };
    setRows([...rows, newRow]);
  };

  // Function to handle input change in rows
  const handleInputChange = (id, value) => {
    setRows(rows.map(row => (row.id === id ? { ...row, value: value } : row)));
  };

  return (
    <div>
      <table>
        <thead>
          <tr>
            <th>#</th>
            <th>Value</th>
          </tr>
        </thead>
        <tbody>
          {rows.map((row) => (
            <tr key={row.id}>
              <td>{row.id}</td>
              <td>
                <input
                  type="text"
                  value={row.value}
                  onChange={(e) => handleInputChange(row.id, e.target.value)}
                />
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      <button onClick={addRow}>Add Row</button>
    </div>
  );
};

